import DefaultConfigurator from '../../../../scripts/services/defaultConfigurator';

module.exports = function (module) {
    module.factory('Configurator', ['gettextCatalog', function (gettextCatalog) {

        class Configurator extends DefaultConfigurator{

            constructor() {
                super();                
            }
           
            get hideAccountText() { return true; }
            get hideAboutMenuEntry() { return true; }
            get regionsOnlyNearestFree() { return true; }
            get darkThemeAvailable() {return false;}
            get showAboutLink() { return true; }
            get showQuitLink() { return true; }
            get showLogoutLink() { return true; }
            get showThemeSelection() { return false; }
            get useOsTheme() { return false; }

            get feedbackUrlMac() { return "https://www.invincibull.io/contact/"; }
            get feedbackUrlWin() { return "https://www.invincibull.io/contact/"; }

            supportUrl (lang) {
                return "https://www.invincibull.io/faq/";
            }

            aboutUrl (lang) {
                return "https://www.invincibull.io/";
            }

            getLabels() {
                return {
                    ProductName : "Invincibull",
                    BrandName: "Invincibull",
                    SublogoTextPro: "Unlimited",
                    ContextMenuName: "InvinciBull.",
                    ProBadge: "UNLIMITED"
                };
            }            

            getStrings() {
                return {
                    getPro : gettextCatalog.getString("Get Unlimited"),
                    disconnect: gettextCatalog.getString('DISCONNECT'),
                    secureMyConnection: gettextCatalog.getString('SECURE MY CONNECTION'),
                    cancel: gettextCatalog.getString('CANCEL')
                };
            }
        }

        return new Configurator();
    }]);
};