module.exports = function (module) {
    module.factory('AppHostStorage', ['MessageBus', function (MessageBus) {
        class AppHostStorage {

            constructor(messageBus) {
                this.messageBus = messageBus;
            }

            set(name, value) {
                var self = this;
                return new Promise((resolve) => {
                    self.messageBus.request(self.messageBus.STORAGE_SET, {
                        key: name,
                        value: value
                    }, () => {
                        resolve();
                    });
                });
            }

            get(name) {
                var self = this;
                return new Promise((resolve, reject) => {
                    self.messageBus.request(self.messageBus.STORAGE_GET, name, (message) => {
                        if (message && message.result) {
                            resolve(message.result);
                        } else {
                            reject();
                        }
                    });
                });
            }

            remove(name) {
                var self = this;
                return new Promise((resolve) => {
                    self.messageBus.request(self.messageBus.STORAGE_REMOVE, name, () => {
                        resolve();
                    });
                });
            }
        }

        return new AppHostStorage(MessageBus);
    }]);
};