module.exports = function (app) {
	app.directive('privacy', function() {
		return {
			templateUrl: 'views/directives/privacy.html',
			replace: true,
			scope: {},
			controller:  ['$scope', 'MessageBus', 'gettextCatalog', 'AppHostStorage', 'oe', function ($scope, MessageBus, gettextCatalog, AppHostStorage, oe) {

				$(".nano").nanoScroller();
				$(".nano-pane").css("display","block");
				$(".nano-slider").css("display","block");

				MessageBus.on(MessageBus.VPN_CHANGEVIEW, function (newView) {
                    if (newView == "privacyView") {
						setTimeout(function() {
							$('.nano').nanoScroller();
						  }, 0);
                    }
                });
				
				$scope.agreeAndContinue = () => {
					AppHostStorage.set("eula_accepted", {
						accepted : true
					});
					
					oe.sendGdprConsentToConnect();
					MessageBus.trigger(MessageBus.VPN_CHANGEVIEW, "diagnosticsView");
				};

				$scope.openEula = () => {
                    var lang = gettextCatalog.getCurrentLanguage().substr(0, 2);
                    var url = `https://www.avira.com/${lang}/end-user-license-agreement-terms-of-use`;
                    MessageBus.appHostRequest(MessageBus.OPEN_URL_IN_DEFAULT_BROWSER, { url: url });
				};

				$scope.openTermsAndConditions = () => {
                    var lang = gettextCatalog.getCurrentLanguage().substr(0, 2);
                    var url = `https://www.avira.com/${lang}/standard-terms-conditions-business`;
                    MessageBus.appHostRequest(MessageBus.OPEN_URL_IN_DEFAULT_BROWSER, { url: url });
				};

				$scope.getPrivacyPolicyLink = () => {
					var lang = gettextCatalog.getCurrentLanguage().substr(0, 2);
					return `https://www.avira.com/${lang}/general-privacy`;
				};

				$scope.openPrivacyAndPolicy = () => {
                    MessageBus.appHostRequest(MessageBus.OPEN_URL_IN_DEFAULT_BROWSER, { url: $scope.getPrivacyPolicyLink() });
				};
            }]
		};
	});
};