module.exports = function (module) {
    module.factory('Features', ['MessageBus', function (MessageBus) {
        class Features {

            constructor(messageBus) {
                this.messageBus = messageBus;
                this.subscribeToFeatures();
                this.requestFeatures();
            }

            subscribeToFeatures() {
                this.messageBus.subscribe(this.messageBus.FEATURES, (message) => {
                    this.features = message.params;
                });
            }

            requestFeatures() {
                this.messageBus.request(this.messageBus.FEATURES, (message) => {
                    this.features = message.result;
                });
            }

            get getFeatures() {
                if (this.features) {
                    return this.features;
                }

                return {};
            }
        }

        return new Features(MessageBus);
    }]);
};