module.exports = function (app) {
    app.directive('bindHtmlUnsafe', function() {
        function link(scope, element, attrs) {
    
            var update = function() {
                element.html(scope.html);
            }
    
            attrs.$observe('bindHtmlUnsafe', function(value) {
                update();
            });
        }
    
        return {
            link: link,
            scope:  {
                html:   '='
            }
        };
    });
}