module.exports = function (module) {
    module.factory('License', ['MessageBus', function (MessageBus) {
        class License {

            constructor(messageBus) {
                this.messageBus = messageBus;
                this.license = null;
                this.subscribeToUserLicense();
                this.requestUserLicense();
            }

            subscribeToUserLicense() {
                this.messageBus.subscribe(this.messageBus.USER_LICENSE, (message) => {
                    this.license = message.params;
                    MessageBus.trigger(MessageBus.VPN_LICENSE_CHANGED);
                });
            }

            requestUserLicense() {
                this.messageBus.request(this.messageBus.USER_LICENSE, (message) => {
                    this.license = message.result;
                    MessageBus.trigger(MessageBus.VPN_LICENSE_CHANGED);
                });
            }

            /**
             * Used as enum for possible license types.
             *
             * @readonly
             * @memberof License
             */
            get Type() {
                return {
                    Unregistered: "Unregistered",
                    Registered: "Registered",
                    Pro: "Pro"
                };
            }

            getLicenseType() {
                if (this.license) {
                    return this.license.LicenseType;
                }

                return "Unregistered";
            }

            getLicense() {
                return this.license;
            }

            getTrafficInterval() {
                if (this.license) {
                    return this.license.traffic_limit_interval;
                }

                return "monthly";
            }
        }

        return new License(MessageBus);
    }]);
};