import DefaultConfigurator from '../../../../scripts/services/defaultConfigurator';

module.exports = function (module) {
    
    module.factory('Configurator', ['gettextCatalog', function (gettextCatalog) {
    
       
        class Configurator extends DefaultConfigurator {

            constructor() {
                super();
            }           

            get useForcedLogin() { return true; }
            get feedbackUrlMac() { return "https://www.avira.com"; }
            get feedbackUrlWin() { return "https://www.avira.com"; }
            get showThemeSelection() { return false; }
            get useOsTheme() { return false; }
            get forceDarkTheme() { return true; }
            get showHelpButton() { return false; }
            get login_forgotPasswordUrl() { return "https://subscriptions.zoho.com/portal/apollo2/forgetpassword"; }
            get login_registerUrl() { return "https://www.joinapollo.cc/"; }
            get emailConfirmationNeeded() { return false; }
            get allowLogout() { return false; }
            get onlyProConnects() { return true; }
            get useOeAuth() { return false; }
            get openDashboardInUI() { return true; }
            get dashboardUrl() { return "https://subscriptions.zoho.com/portal/apollo2"; }
            get renewalUrl() { return "https://www.joinapollo.cc/"; }

            get showFeedbackOnDisconnect() { return true; }

            supportUrl (lang) {
                return "https://www.avira.com/" + lang + "/vpn-support";
            }

            aboutUrl (lang) {
                return "https://www.avira.com/" + lang + "/vpn-legal-info";
            }

            getLabels() {
                return {
                    ProductName : "Apollo",
                    BrandName: "Apollo",
                    SublogoTextPro: "",
                    ContextMenuName: "Apollo.",
                    ProBadge: ""
                };
            }        

            getStrings() {
                return {
                    getPro : gettextCatalog.getString("Get Pro"),
                    disconnect: gettextCatalog.getString('Disconnect'),
                    secureMyConnection: gettextCatalog.getString('Secure my connection'),
                    cancel: gettextCatalog.getString('Cancel')
                };
            }
        }

        return new Configurator();
    }]);
};
