module.exports = function (module) {

    module.factory('Menu', ['MessageBus', 'gettextCatalog', 'Configurator', function (messageBus, gettextCatalog, Configurator) {
        var menu = {
            initialize: function () {
                messageBus.appHostRequest("menu/add", { name: "toggleConnect", index: 0, text: gettextCatalog.getString("Connect") });
                messageBus.appHostRequest("menu/add", { name: "menuExit", index : 1, text: gettextCatalog.getString("Exit") });
            }
        };

        messageBus.subscribe("menuExit", function () {
            messageBus.appHostRequest("closeWindow");
        });

        messageBus.on(messageBus.VPN_STATUS_CHANGED, function (status) {
            var PRODUCT_NAME = Configurator.getLabels().ContextMenuName;
            switch (status) {
                case "Connected":
                    messageBus.appHostRequest("menu/set", { name: "toggleConnect", enabled: true, text: gettextCatalog.getString("Disconnect") });
                    messageBus.appHostRequest("systray/tooltip/set", PRODUCT_NAME + " " + gettextCatalog.getString("Your connection is secure"));
                    break;
                case "Disconnected":
                    messageBus.appHostRequest("menu/set", { name: "toggleConnect", enabled: true, text: gettextCatalog.getString("Connect") });
                    messageBus.appHostRequest("systray/tooltip/set", PRODUCT_NAME + " " + gettextCatalog.getString("Your connection is unsecure"));
                    break;
                case "Connecting":
                    messageBus.appHostRequest("menu/set", { name: "toggleConnect", enabled: false, text: gettextCatalog.getString("Connecting") });
                    messageBus.appHostRequest("systray/tooltip/set", PRODUCT_NAME + " " + gettextCatalog.getString("Connecting"));
                    break;
                case "Disconnecting":
                    messageBus.appHostRequest("menu/set", { name: "toggleConnect", enabled: false, text: gettextCatalog.getString("Disconnecting") });
                    messageBus.appHostRequest("systray/tooltip/set", PRODUCT_NAME + " " + gettextCatalog.getString("Disconnecting"));
                    break;
            }
        });

        return menu;
    }]);

}