angular.module('rate5Stars')
    .directive('rate', function () {
        return {
            templateUrl: 'widgets/rate-5stars/rate.html',
            restrict: 'E',
            replace: true,
            scope: {
                rateTitleText: '@',
                rateDescriptionText: '@',
                rateButtonText: '@',
                notNowButtonText: '@',
                selectableStars: '=',
                oneStarText: '@',
                twoStarsText: '@',
                threeStarsText: '@',
                fourStarsText: '@',
                fiveStarsText: '@',
                showDontShowAgain: '=',
                dontShowAgainText: '@',
                onDontShowAgain:'&',
                onRate: '&',
                onNotNow: '&',
                show: '=',
                alignButtonsHorizontally: '='
            },
            controller: 'rateController'
        };
    });