var OAuth = require('oe-oauth');

var isWindowsClient = (window.external !== undefined && (typeof window.external.SendMessage) !== "undefined");

module.exports = function (module) {
    module.factory('oe', ['MessageBus', 'AppHostStorage', 'Configurator', function (MessageBus, AppHostStorage, Configurator) {

        if(!Configurator.useOeAuth) {
            return null;
        }

        var logger = (message) => {
            MessageBus.trace(message);
        };

        if (isWindowsClient) {
            let mock = {};
            mock.registerDeviceIfNotLoggedIn = () => new Promise((resolve) => resolve(true));
            return mock;
        }

        let oeAuth = new OAuth(Configurator.oeUrl, Configurator.client, Configurator.secret, AppHostStorage, logger);
        let device_data = {};
        let app_data = {};

        oeAuth.sendToken = () => {
            return oeAuth.accessToken()
                .then((token) => {
                    MessageBus.request(MessageBus.ACCESS_TOKEN, token, () => {});
                });
        };

        oeAuth.getDeviceData = () => device_data;
        oeAuth.getAppData = () => app_data;

        MessageBus.subscribe(MessageBus.ACCESS_TOKEN, () => {
            oeAuth.sendToken();
        });

        MessageBus.subscribe(MessageBus.REFRESH_ACCESS_TOKEN, () => {
            oeAuth.refresh();
        });

        let initDeviceData = () => {
            return new Promise(resolve => {
                MessageBus.request("deviceData/get", (m) => {
                    device_data = m.result;
                    resolve();
                });
            });
        };

        let initAppData = () => {
            return new Promise(resolve => {
                MessageBus.request("appData/get", (m) => {
                    app_data = m.result;
                    resolve();
                });
            });
        };

        oeAuth.requestPermanentToken = () => oeAuth.accessToken()
            .then((token) => {
                if (token && token !== "") {
                    MessageBus.trace("Permanent taken already cached in storage.");
                    return new Promise((resolve) => resolve(true));
                }

                MessageBus.trace("Requesting permanent anonym token.");
                return oeAuth.registerDevice(oeAuth.getDeviceData(), oeAuth.getAppData());
            })
            .then(() => oeAuth.sendToken())
            .catch((error) => {
                MessageBus.trace(`Failed to register device: ${JSON.stringify(error)}`);
            });

        let initService = () => {
            MessageBus.trace("Initializing OE Service...");
            initDeviceData()
                .then(() => initAppData())
                .then(() => oeAuth.requestPermanentToken());
        };

        initService();

        oeAuth.sendGdprConsentToConnect = () => {
            MessageBus.trace("Sending the Gdpr consent to Connect Backend...");
            oeAuth.registerDevice(oeAuth.getDeviceData(), oeAuth.getAppData()).
            then(() => {
                oeAuth.storeGdprConsent();
                MessageBus.request(MessageBus.START_DEVICE_PINGER, () => {});
            }).
            catch((error) => {
                MessageBus.trace(JSON.stringify(error));
            });
        };

        let sendGdprPing = (token) => {
            if (token && token !== "") {
                oeAuth.sendDevicePing(token)
                    .then((result) => {
                        MessageBus.trace("Sent device ping for current device.");
                    })
                    .catch((error) => {
                        MessageBus.trace("Failed to send device ping. " + JSON.stringify(error));
                    });
            }
        };

        oeAuth.sendGdprPingToConnect = () => {
            MessageBus.trace("Sending device ping to Connect Backend...");

            if (isWindowsClient) {
                MessageBus.request(MessageBus.ACCESS_TOKEN, (message) => {
                    sendGdprPing(message.result);
                });
            } else {
                oeAuth.accessToken().then((token) => {
                    sendGdprPing(token);
                });
            }
        };

        MessageBus.subscribe(MessageBus.SEND_DEVICE_PING, () => oeAuth.sendGdprPingToConnect());

        MessageBus.subscribe(MessageBus.NEW_ACCESS_TOKEN, (message) => {
            MessageBus.trace(`Received new access token. Reinitializing oeauth library...`);
            let payload = JSON.parse(message.params);
            oeAuth.handleOAuthResponse(payload, '').then(() => oeAuth.sendToken());
        });

        return oeAuth;
    }]);
};